import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			groupons: [],
			limit: 100,
			totalPages: 1
		}
	},
	onLoad: function(options) {
		this.getBrandList();
	  },
	  onPullDownRefresh() {
		this.getBrandList(true);
	  },
	methods: {
	  getBrandList: function(flag) {
		  uni.showLoading({
			title: '加载中...',
		  });
		  let that = this;
		  api.httpget('/groupon/list?page=1&limit='+this.limit).then(function(res) {
			if (res.data.errno === 0) {
				that.groupons = res.data.data.list;
				that.totalPages = res.data.data.pages
			}
			if(flag) uni.stopPullDownRefresh()
			uni.hideLoading();
		  })
		}
	}
}
